import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import {
  PageSection,
  Title,
  Breadcrumb,
  BreadcrumbItem,
  Stack,
  StackItem,
  Content,
  Progress,
  ProgressMeasureLocation,
  Button,
  ActionGroup,
  Flex,
  FlexItem,
  Spinner,
  Grid,
  GridItem,
  Icon,
  Card,
  CardTitle,
  CardBody,
  Pagination,
} from '@patternfly/react-core';
import { Table, Thead, Tbody, Tr, Th, Td } from '@patternfly/react-table';
import { ExclamationTriangleIcon, CheckCircleIcon, ClockIcon, PauseIcon, FileAltIcon } from '@patternfly/react-icons';

const UpgradeProgress: React.FunctionComponent = () => {
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = React.useState<string>('current-blockers');
  const [canContinue, setCanContinue] = React.useState(false);
  const [blockerPage, setBlockerPage] = React.useState(1);
  const [blockerPerPage, setBlockerPerPage] = React.useState(10);
  const [operatorPage, setOperatorPage] = React.useState(1);
  const [operatorPerPage, setOperatorPerPage] = React.useState(10);
  const [nodePage, setNodePage] = React.useState(1);
  const [nodePerPage, setNodePerPage] = React.useState(10);

  // Enable Next button after 5 seconds
  React.useEffect(() => {
    const timer = setTimeout(() => {
      setCanContinue(true);
    }, 5000);

    return () => clearTimeout(timer);
  }, []);

  const verticalTabs = [
    { id: 'current-blockers', label: 'Current Blockers', completed: false, statusIcon: <ExclamationTriangleIcon style={{ color: 'var(--pf-t--global--warning-color--100)' }} /> },
    { id: 'operator-upgrade', label: 'Operator upgrade details', completed: false, statusIcon: <Spinner size="sm" /> },
    { id: 'worker-nodes', label: 'Worker nodes upgrade details', completed: false, statusIcon: <Spinner size="sm" /> },
  ];

  const blockerColumns = ['Issue', 'Actions'];
  const allBlockerRows = [
    {
      issue: 'Node worker-south cannot drain PodDisruptionBudget',
      actions: (
        <ActionGroup>
          <Button variant="link" isInline>
            View logs
          </Button>
          <Button variant="link" isInline>
            Remediate
          </Button>
        </ActionGroup>
      ),
    },
    {
      issue: 'Operator Another op 4 is degraded',
      actions: (
        <ActionGroup>
          <Button variant="link" isInline>
            View logs
          </Button>
          <Button variant="link" isInline>
            Remediate
          </Button>
        </ActionGroup>
      ),
    },
    {
      issue: 'Required image cannot be pulled',
      actions: (
        <ActionGroup>
          <Button variant="link" isInline>
            View logs
          </Button>
          <Button variant="link" isInline>
            Remediate
          </Button>
        </ActionGroup>
      ),
    },
    {
      issue: 'Additional blocker 1',
      actions: (
        <ActionGroup>
          <Button variant="link" isInline>
            View logs
          </Button>
          <Button variant="link" isInline>
            Remediate
          </Button>
        </ActionGroup>
      ),
    },
  ];

  const operatorColumns = ['Operator', 'Status'];
  const allOperatorRows = [
    { operator: 'Another op 1', status: 'Progressing', statusIcon: <Spinner size="sm" /> },
    { operator: 'Another op 2', status: 'Available', statusIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
    { operator: 'Another op 3', status: 'Progressing', statusIcon: <Spinner size="sm" /> },
    { operator: 'Another op 4', status: 'Available', statusIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
  ];

  const workerNodeColumns = ['Worker Node', 'Upgrade Stage'];
  const allWorkerNodeRows = [
    { node: 'worker-west', stage: 'Rebooting', stageIcon: <Spinner size="sm" /> },
    { node: 'worker-east', stage: 'Pending (Not Started)', stageIcon: <Icon><ClockIcon /></Icon> },
    { node: 'worker-north', stage: 'Rebooting', stageIcon: <Spinner size="sm" /> },
    { node: 'worker-south', stage: 'Pending (Not Started)', stageIcon: <Icon><ClockIcon /></Icon> },
  ];

  return (
    <>
      <PageSection hasBodyWrapper={false}>
        <Breadcrumb>
          <BreadcrumbItem to="#" onClick={() => navigate('/administration/updates')}>
            Update overview
          </BreadcrumbItem>
          <BreadcrumbItem isActive>Cluster Updates</BreadcrumbItem>
        </Breadcrumb>
        <Flex justifyContent={{ default: 'justifyContentSpaceBetween' }} alignItems={{ default: 'alignItemsCenter' }} style={{ marginTop: 'var(--pf-t--global--spacer--md)' }}>
          <FlexItem>
            <Title headingLevel="h1" size="lg">
              Cluster Updates
            </Title>
          </FlexItem>
          <FlexItem>
            <Flex alignItems={{ default: 'alignItemsCenter' }} gap={{ default: 'gapMd' }}>
              <FlexItem>
                <Button 
                  variant="primary" 
                  onClick={() => navigate('/administration/updates/update-overview')}
                  isDisabled={!canContinue}
                >
                  Done
                </Button>
              </FlexItem>
              <FlexItem>
                <Button variant="secondary" icon={<PauseIcon />}>
                  Pause
                </Button>
              </FlexItem>
              <FlexItem>
                <Button variant="secondary" onClick={() => navigate('/administration/updates/preflight-summary')}>
                  Cancel
                </Button>
              </FlexItem>
              <FlexItem>
                <Button variant="link" isInline icon={<FileAltIcon />}>
                  View logs
                </Button>
              </FlexItem>
            </Flex>
          </FlexItem>
        </Flex>
      </PageSection>

      {/* Status and Summary Section - Outside Card, at top of page */}
      <PageSection hasBodyWrapper={false}>
        <Stack hasGutter>
          {/* Upgrade Progress Title */}
          <StackItem>
            <Flex alignItems={{ default: 'alignItemsCenter' }}>
              <FlexItem>
                <Spinner size="md" />
              </FlexItem>
              <FlexItem>
                <Title headingLevel="h2" size="lg">Upgrade to 5.00.01 Progress</Title>
              </FlexItem>
            </Flex>
            <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
              <strong>Cluster:</strong> 1c182977-5663-428d-92a3-3d2bdf3fffb6
            </Content>
            <Content style={{ marginTop: 'var(--pf-t--global--spacer--xs)' }}>
              <strong>Start time:</strong> 2025-11-14 21:32:00
            </Content>
            <Content style={{ marginTop: 'var(--pf-t--global--spacer--xs)' }}>
              Estimated time remaining: 84 min
            </Content>
            <Content style={{ marginTop: 'var(--pf-t--global--spacer--xs)' }}>
              <strong>Estimated finish time:</strong> 2025-11-14 22:56:00
            </Content>
          </StackItem>

          {/* Progress Bars - Clean and Pretty */}
          <StackItem>
            <Grid hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--lg)' }}>
              <GridItem span={4}>
                <div style={{ marginBottom: 'var(--pf-t--global--spacer--sm)' }}>
                  <Title headingLevel="h4" size="md">Control Plane</Title>
                </div>
                <div style={{ position: 'relative' }}>
                  <Progress
                    value={100}
                    measureLocation={ProgressMeasureLocation.inside}
                    label="Complete"
                    aria-label="Control Plane progress"
                    style={{ height: '24px' }}
                  />
                </div>
              </GridItem>
              <GridItem span={4}>
                <div style={{ marginBottom: 'var(--pf-t--global--spacer--sm)' }}>
                  <Title headingLevel="h4" size="md">Core Operators</Title>
                </div>
                <div style={{ position: 'relative' }}>
                  <Progress
                    value={56}
                    measureLocation={ProgressMeasureLocation.inside}
                    aria-label="Core Operators progress"
                    style={{ height: '24px' }}
                  />
                </div>
              </GridItem>
              <GridItem span={4}>
                <div style={{ marginBottom: 'var(--pf-t--global--spacer--sm)' }}>
                  <Title headingLevel="h4" size="md">Workers</Title>
                </div>
                <div style={{ position: 'relative' }}>
                  <Progress
                    value={0}
                    measureLocation={ProgressMeasureLocation.inside}
                    aria-label="Workers progress"
                    className="pf-v6-c-progress--zero-percent"
                    style={{ height: '24px' }}
                  />
                </div>
              </GridItem>
            </Grid>
          </StackItem>
        </Stack>
      </PageSection>

      <PageSection hasBodyWrapper={false}>
        <Card>
          <CardTitle>Upgrade Details</CardTitle>
          <CardBody>
            <Grid hasGutter>
              {/* Left Vertical Navigation Panel */}
              <GridItem span={3}>
                <div style={{ 
                  backgroundColor: 'var(--pf-t--global--BackgroundColor--200)', 
                  padding: 'var(--pf-t--global--spacer--md)',
                  borderRadius: 'var(--pf-t--global--BorderRadius--sm)',
                  height: '600px',
                  overflowY: 'auto'
                }}>
                  <Stack hasGutter>
                    {verticalTabs.map((tab) => (
                      <StackItem key={tab.id}>
                        <div
                          onClick={() => setActiveTab(tab.id)}
                          style={{
                            padding: 'var(--pf-t--global--spacer--sm) var(--pf-t--global--spacer--md)',
                            cursor: 'pointer',
                            backgroundColor: activeTab === tab.id ? 'var(--pf-t--global--BackgroundColor--100)' : 'transparent',
                            borderLeft: activeTab === tab.id ? '4px solid #0066CC' : '4px solid transparent',
                            borderRadius: 'var(--pf-t--global--BorderRadius--sm)',
                            display: 'flex',
                            alignItems: 'center',
                            gap: 'var(--pf-t--global--spacer--sm)',
                            transition: 'all 0.2s ease',
                          }}
                        >
                          {tab.statusIcon && (
                            <div style={{ flexShrink: 0, display: 'flex', alignItems: 'center' }}>
                              {tab.statusIcon}
                            </div>
                          )}
                          {tab.completed && (
                            <CheckCircleIcon style={{ 
                              color: '#3E8635', 
                              fontSize: 'var(--pf-t--global--FontSize--md)',
                              flexShrink: 0,
                            }} />
                          )}
                          <Content style={{ 
                            color: activeTab === tab.id ? '#0066CC' : 'var(--pf-t--global--Color--100)',
                            fontWeight: activeTab === tab.id ? 600 : 400,
                          }}>
                            {tab.label}
                          </Content>
                        </div>
                      </StackItem>
                    ))}
                  </Stack>
                </div>
              </GridItem>

              {/* Right Content Area */}
              <GridItem span={9}>
                {activeTab === 'current-blockers' && (
                  <Stack hasGutter>
                    <StackItem>
                      <Title headingLevel="h2" size="lg">Current Blockers</Title>
                    </StackItem>
                    <StackItem>
                      <Table>
                        <Thead>
                          <Tr>
                            {blockerColumns.map((column) => (
                              <Th key={column}>{column}</Th>
                            ))}
                          </Tr>
                        </Thead>
                        <Tbody>
                          {(() => {
                            const startIdx = (blockerPage - 1) * blockerPerPage;
                            const endIdx = startIdx + blockerPerPage;
                            const paginatedRows = allBlockerRows.slice(startIdx, endIdx);
                            return paginatedRows.map((row, index) => (
                              <Tr key={index}>
                                <Td>
                                  <Flex>
                                    <FlexItem>
                                      <ExclamationTriangleIcon style={{ color: 'var(--pf-t--global--warning-color--100)' }} />
                                    </FlexItem>
                                    <FlexItem>{row.issue}</FlexItem>
                                  </Flex>
                                </Td>
                                <Td>{row.actions}</Td>
                              </Tr>
                            ));
                          })()}
                        </Tbody>
                      </Table>
                    </StackItem>
                    {allBlockerRows.length > blockerPerPage && (
                      <StackItem>
                        <Pagination
                          itemCount={allBlockerRows.length}
                          perPage={blockerPerPage}
                          page={blockerPage}
                          onSetPage={(_, page) => setBlockerPage(page)}
                          onPerPageSelect={(_, perPage) => {
                            setBlockerPerPage(perPage);
                            setBlockerPage(1);
                          }}
                        />
                      </StackItem>
                    )}
                  </Stack>
                )}

                {activeTab === 'operator-upgrade' && (
                  <Stack hasGutter>
                    <StackItem>
                      <Title headingLevel="h2" size="lg">Operator upgrade details</Title>
                    </StackItem>
                    <StackItem>
                      <Table>
                        <Thead>
                          <Tr>
                            {operatorColumns.map((column) => (
                              <Th key={column}>{column}</Th>
                            ))}
                          </Tr>
                        </Thead>
                        <Tbody>
                          {(() => {
                            const startIdx = (operatorPage - 1) * operatorPerPage;
                            const endIdx = startIdx + operatorPerPage;
                            const paginatedRows = allOperatorRows.slice(startIdx, endIdx);
                            return paginatedRows.map((row, index) => (
                              <Tr key={index}>
                                <Td>{row.operator}</Td>
                                <Td>
                                  <Flex alignItems={{ default: 'alignItemsCenter' }}>
                                    <FlexItem>{row.statusIcon}</FlexItem>
                                    <FlexItem>{row.status}</FlexItem>
                                  </Flex>
                                </Td>
                              </Tr>
                            ));
                          })()}
                        </Tbody>
                      </Table>
                    </StackItem>
                    {allOperatorRows.length > operatorPerPage && (
                      <StackItem>
                        <Pagination
                          itemCount={allOperatorRows.length}
                          perPage={operatorPerPage}
                          page={operatorPage}
                          onSetPage={(_, page) => setOperatorPage(page)}
                          onPerPageSelect={(_, perPage) => {
                            setOperatorPerPage(perPage);
                            setOperatorPage(1);
                          }}
                        />
                      </StackItem>
                    )}
                  </Stack>
                )}

                {activeTab === 'worker-nodes' && (
                  <Stack hasGutter>
                    <StackItem>
                      <Title headingLevel="h2" size="lg">Worker nodes upgrade details</Title>
                    </StackItem>
                    <StackItem>
                      <Table>
                        <Thead>
                          <Tr>
                            {workerNodeColumns.map((column) => (
                              <Th key={column}>{column}</Th>
                            ))}
                          </Tr>
                        </Thead>
                        <Tbody>
                          {(() => {
                            const startIdx = (nodePage - 1) * nodePerPage;
                            const endIdx = startIdx + nodePerPage;
                            const paginatedRows = allWorkerNodeRows.slice(startIdx, endIdx);
                            return paginatedRows.map((row, index) => (
                              <Tr key={index}>
                                <Td>{row.node}</Td>
                                <Td>
                                  <Flex alignItems={{ default: 'alignItemsCenter' }}>
                                    <FlexItem>{row.stageIcon}</FlexItem>
                                    <FlexItem>{row.stage}</FlexItem>
                                  </Flex>
                                </Td>
                              </Tr>
                            ));
                          })()}
                        </Tbody>
                      </Table>
                    </StackItem>
                    {allWorkerNodeRows.length > nodePerPage && (
                      <StackItem>
                        <Pagination
                          itemCount={allWorkerNodeRows.length}
                          perPage={nodePerPage}
                          page={nodePage}
                          onSetPage={(_, page) => setNodePage(page)}
                          onPerPageSelect={(_, perPage) => {
                            setNodePerPage(perPage);
                            setNodePage(1);
                          }}
                        />
                      </StackItem>
                    )}
                  </Stack>
                )}
              </GridItem>
            </Grid>
          </CardBody>
        </Card>
      </PageSection>
    </>
  );
};

export { UpgradeProgress };

