import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import {
  PageSection,
  Title,
  Breadcrumb,
  BreadcrumbItem,
  Stack,
  StackItem,
  Content,
  Grid,
  GridItem,
  TextArea,
  Button,
  ActionGroup,
  Alert,
  Divider,
  Flex,
  FlexItem,
  Select,
  SelectOption,
  Icon,
  MenuToggle,
  Card,
  CardTitle,
  CardBody,
  Tabs,
  Tab,
  TabContent,
  TabTitleText,
} from '@patternfly/react-core';
import { Table, Thead, Tbody, Tr, Th, Td } from '@patternfly/react-table';
import { ExclamationTriangleIcon, InfoCircleIcon, CheckCircleIcon } from '@patternfly/react-icons';

const PreflightSummary: React.FunctionComponent = () => {
  const navigate = useNavigate();
  const [yamlContent, setYamlContent] = React.useState(`apiVersion: v1
kind: ClusterUpgrade
metadata:
  name: cluster-upgrade-preflight
spec:
  currentVersion: 4.18.16
  targetVersion: 5.00.01
  clusterId: ocp-cluster-abc123-def456-ghi789
  upgradeSequence:
    - control-plane
    - core-operators
    - workers
    - optional-operators
`);

  const [maxUnavailable, setMaxUnavailable] = React.useState('1');
  const [isMaxUnavailableOpen, setIsMaxUnavailableOpen] = React.useState(false);
  const [activeTab, setActiveTab] = React.useState<string>('node-capacity');

  const verticalTabs = [
    { id: 'node-capacity', label: 'Node capacity', completed: true },
    { id: 'cluster-health', label: 'Cluster health', completed: true },
    { id: 'api-changes', label: 'API changes in next upgrade', completed: true },
    { id: 'operator-readiness', label: 'Operator readiness', completed: true },
  ];

  const clusterHealthColumns = ['Check', 'Status'];
  const clusterHealthRows = [
    { check: 'All nodes healthy', status: 'Pass' },
    { check: 'Storage available', status: 'Pass' },
    { check: 'Network connectivity', status: 'Pass' },
    { check: 'API server responsive', status: 'Pass' },
  ];

  const apiChangesColumns = ['API', 'Status', 'Action Required'];
  const apiChangesRows = [
    { api: 'apps/v1 Deployment', status: 'Ready', action: 'None' },
    { api: 'networking.k8s.io/v1 Ingress', status: 'Ready', action: 'None' },
    { api: 'rbac.authorization.k8s.io/v1 Role', status: 'Review', action: 'Verify permissions' },
  ];

  const operatorReadinessColumns = ['Operator', 'Status', 'Version'];
  const operatorReadinessRows = [
    { operator: 'Red Hat OpenShift Service Mesh', status: 'Ready', version: '2.4.0' },
    { operator: 'Red Hat OpenShift Logging', status: 'Ready', version: '5.8.0' },
    { operator: 'Red Hat OpenShift GitOps', status: 'Pending', version: '1.11.0' },
  ];

  const handleCopyYaml = () => {
    navigator.clipboard.writeText(yamlContent);
  };

  const handleDownloadYaml = () => {
    const blob = new Blob([yamlContent], { type: 'text/yaml' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'preflight-summary.yaml';
    a.click();
    URL.revokeObjectURL(url);
  };

  const handleExportAuditPlan = () => {
    // Placeholder for export audit plan functionality
    console.log('Export audit plan');
  };

  const handleExportPreflightYaml = () => {
    handleDownloadYaml();
  };

  return (
    <>
      <PageSection hasBodyWrapper={false}>
        <Breadcrumb>
          <BreadcrumbItem to="#" onClick={() => navigate('/administration/updates')}>
            Updates
          </BreadcrumbItem>
          <BreadcrumbItem isActive>Preflight Summary</BreadcrumbItem>
        </Breadcrumb>
        <Flex justifyContent={{ default: 'justifyContentSpaceBetween' }} alignItems={{ default: 'alignItemsCenter' }} style={{ marginTop: 'var(--pf-t--global--spacer--md)' }}>
          <FlexItem>
            <Title headingLevel="h1" size="lg">
              Cluster updates - Preflight summary
            </Title>
          </FlexItem>
          <FlexItem>
            <ActionGroup>
              <Button variant="primary" onClick={() => navigate('/administration/updates/upgrade-progress')}>
                Run upgrade
              </Button>
              <Button variant="secondary" onClick={handleExportAuditPlan}>
                Export audit plan
              </Button>
              <Button variant="secondary" onClick={() => navigate('/administration/updates')}>
                Cancel
              </Button>
            </ActionGroup>
          </FlexItem>
        </Flex>
      </PageSection>

      {/* Summary Section - Outside Card, at top of page */}
      <PageSection hasBodyWrapper={false}>
        <Stack hasGutter>
          <StackItem>
            <Title headingLevel="h2" size="lg">Summary</Title>
          </StackItem>
          <StackItem>
            <Title headingLevel="h4" size="md">Current cluster ID</Title>
            <Content>ocp-cluster-abc123-def456-ghi789</Content>
          </StackItem>
          <StackItem>
            <Grid hasGutter>
              <GridItem span={6}>
                <Title headingLevel="h4" size="md">Current version</Title>
                <Content>4.18.16</Content>
              </GridItem>
              <GridItem span={6}>
                <Title headingLevel="h4" size="md">Targeted stable release</Title>
                <Content>fast-5.00.01</Content>
              </GridItem>
            </Grid>
          </StackItem>
          <StackItem>
            <Title headingLevel="h4" size="md">Estimated duration</Title>
            <Content>Typical time 30-90 minutes</Content>
          </StackItem>
          <StackItem>
            <Title headingLevel="h4" size="md">Upgrade sequence</Title>
            <Content>Control plane  Core operators  Workers  Optional operators</Content>
          </StackItem>
          <StackItem>
            <Alert variant="warning" title="Warning: rolling upgrades going to take specified nodes offline" />
          </StackItem>
        </Stack>
      </PageSection>

      <PageSection hasBodyWrapper={false}>
        <Grid hasGutter>
          {/* Left Side - Card with vertical tabs */}
          <GridItem span={9}>
            <Card>
              <CardTitle>Preflight Summary</CardTitle>
              <CardBody>
                <Grid hasGutter>
                  {/* Left Vertical Navigation Panel */}
                  <GridItem span={3}>
                    <div style={{ 
                      backgroundColor: 'var(--pf-t--global--BackgroundColor--200)', 
                      padding: 'var(--pf-t--global--spacer--md)',
                      borderRadius: 'var(--pf-t--global--BorderRadius--sm)',
                      height: '600px',
                      overflowY: 'auto'
                    }}>
                      <Stack hasGutter>
                        {verticalTabs.map((tab) => (
                          <StackItem key={tab.id}>
                            <div
                              onClick={() => setActiveTab(tab.id)}
                              style={{
                                padding: 'var(--pf-t--global--spacer--sm) var(--pf-t--global--spacer--md)',
                                cursor: 'pointer',
                                backgroundColor: activeTab === tab.id ? 'var(--pf-t--global--BackgroundColor--100)' : 'transparent',
                                borderLeft: activeTab === tab.id ? '4px solid #0066CC' : '4px solid transparent',
                                borderRadius: 'var(--pf-t--global--BorderRadius--sm)',
                                display: 'flex',
                                alignItems: 'center',
                                gap: 'var(--pf-t--global--spacer--sm)',
                                transition: 'all 0.2s ease',
                              }}
                            >
                              {tab.completed && (
                                <CheckCircleIcon style={{ 
                                  color: '#3E8635', 
                                  fontSize: 'var(--pf-t--global--FontSize--md)',
                                  flexShrink: 0,
                                }} />
                              )}
                              <Content style={{ 
                                color: activeTab === tab.id ? '#0066CC' : 'var(--pf-t--global--Color--100)',
                                fontWeight: activeTab === tab.id ? 600 : 400,
                              }}>
                                {tab.label}
                              </Content>
                            </div>
                          </StackItem>
                        ))}
                      </Stack>
                    </div>
                  </GridItem>

                  {/* Right Content Area */}
                  <GridItem span={9}>
                    <Stack hasGutter>
                      {/* Tab Content */}
                      <StackItem>
                        {activeTab === 'node-capacity' && (
                          <Stack hasGutter>
                            <StackItem>
                              <Title headingLevel="h2" size="lg">Node capacity</Title>
                              <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                Ensures the cluster can safely drain and update worker nodes during the upgrade.
                              </Content>
                            </StackItem>
                            <StackItem>
                              <Grid hasGutter>
                                <GridItem span={6}>
                                  <Flex alignItems={{ default: 'alignItemsFlexStart' }}>
                                    <FlexItem>
                                      <Icon status="warning" style={{ marginTop: '4px' }}>
                                        <ExclamationTriangleIcon />
                                      </Icon>
                                    </FlexItem>
                                    <FlexItem>
                                      <Title headingLevel="h4" size="md">Max Unavailable Node Configuration</Title>
                                      <Stack hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                        <StackItem>
                                          <Content>
                                            <strong>Current setting:</strong>
                                          </Content>
                                          <div style={{ marginTop: '4px' }}>
                                            <Select
                                              toggle={(toggleRef) => (
                                                <MenuToggle
                                                  ref={toggleRef}
                                                  onClick={() => setIsMaxUnavailableOpen(!isMaxUnavailableOpen)}
                                                  isExpanded={isMaxUnavailableOpen}
                                                  variant="default"
                                                >
                                                  {maxUnavailable}
                                                </MenuToggle>
                                              )}
                                              isOpen={isMaxUnavailableOpen}
                                              onOpenChange={(isOpen: boolean) => setIsMaxUnavailableOpen(isOpen)}
                                              selected={maxUnavailable}
                                              onSelect={(_, value) => {
                                                setMaxUnavailable(value as string);
                                                setIsMaxUnavailableOpen(false);
                                              }}
                                            >
                                              <SelectOption value="1">1</SelectOption>
                                              <SelectOption value="2">2</SelectOption>
                                              <SelectOption value="3">3</SelectOption>
                                              <SelectOption value="4">4</SelectOption>
                                              <SelectOption value="5">5</SelectOption>
                                            </Select>
                                          </div>
                                        </StackItem>
                                        <StackItem>
                                          <Content>
                                            <strong>Recommended: 5</strong>
                                          </Content>
                                        </StackItem>
                                        <StackItem>
                                          <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                            Allowing more nodes to be updated at once speeds up the upgrade, as long as your workloads can handle it.
                                          </Content>
                                        </StackItem>
                                      </Stack>
                                    </FlexItem>
                                  </Flex>
                                </GridItem>
                                <GridItem span={6}>
                                  <Flex alignItems={{ default: 'alignItemsFlexStart' }}>
                                    <FlexItem>
                                      <Icon status="danger" style={{ marginTop: '4px' }}>
                                        <ExclamationTriangleIcon />
                                      </Icon>
                                    </FlexItem>
                                    <FlexItem>
                                      <Title headingLevel="h4" size="md">Available Capacity per Worker Pool</Title>
                                      <Stack hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                        <StackItem>
                                          <Content>
                                            You need 1 XL node worth of free capacity per worker pool.
                                          </Content>
                                        </StackItem>
                                        <StackItem>
                                          <Content>
                                            <strong>Total free capacity required: 8 nodes</strong>
                                          </Content>
                                        </StackItem>
                                        <StackItem>
                                          <Content>
                                            <strong>Available capacity detected: 3 nodes</strong>
                                          </Content>
                                        </StackItem>
                                        <StackItem>
                                          <Button variant="link" isInline>
                                            Scale up nodes
                                          </Button>
                                        </StackItem>
                                      </Stack>
                                    </FlexItem>
                                  </Flex>
                                </GridItem>
                              </Grid>
                            </StackItem>
                          </Stack>
                        )}

                        {activeTab === 'cluster-health' && (
                          <Stack hasGutter>
                            <StackItem>
                              <Title headingLevel="h2" size="lg">Cluster health</Title>
                              <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                Review the health status of your cluster components before proceeding with the upgrade.
                              </Content>
                            </StackItem>
                            <StackItem>
                              <Table>
                                <Thead>
                                  <Tr>
                                    {clusterHealthColumns.map((column) => (
                                      <Th key={column}>{column}</Th>
                                    ))}
                                  </Tr>
                                </Thead>
                                <Tbody>
                                  {clusterHealthRows.map((row, index) => (
                                    <Tr key={index}>
                                      <Td>{row.check}</Td>
                                      <Td>{row.status}</Td>
                                    </Tr>
                                  ))}
                                </Tbody>
                              </Table>
                            </StackItem>
                          </Stack>
                        )}

                        {activeTab === 'api-changes' && (
                          <Stack hasGutter>
                            <StackItem>
                              <Title headingLevel="h2" size="lg">API changes in next upgrade</Title>
                              <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                Review API changes that will occur during the upgrade. Some APIs may require action before upgrading.
                              </Content>
                            </StackItem>
                            <StackItem>
                              <Table>
                                <Thead>
                                  <Tr>
                                    {apiChangesColumns.map((column) => (
                                      <Th key={column}>{column}</Th>
                                    ))}
                                  </Tr>
                                </Thead>
                                <Tbody>
                                  {apiChangesRows.map((row, index) => (
                                    <Tr key={index}>
                                      <Td>{row.api}</Td>
                                      <Td>{row.status}</Td>
                                      <Td>{row.action}</Td>
                                    </Tr>
                                  ))}
                                </Tbody>
                              </Table>
                            </StackItem>
                          </Stack>
                        )}

                        {activeTab === 'operator-readiness' && (
                          <Stack hasGutter>
                            <StackItem>
                              <Title headingLevel="h2" size="lg">Operator readiness</Title>
                            </StackItem>
                            <StackItem>
                              <Table>
                                <Thead>
                                  <Tr>
                                    {operatorReadinessColumns.map((column) => (
                                      <Th key={column}>{column}</Th>
                                    ))}
                                  </Tr>
                                </Thead>
                                <Tbody>
                                  {operatorReadinessRows.map((row, index) => (
                                    <Tr key={index}>
                                      <Td>{row.operator}</Td>
                                      <Td>{row.status}</Td>
                                      <Td>{row.version}</Td>
                                    </Tr>
                                  ))}
                                </Tbody>
                              </Table>
                            </StackItem>
                          </Stack>
                        )}
                      </StackItem>
                    </Stack>
                  </GridItem>
                </Grid>
              </CardBody>
            </Card>
          </GridItem>

          {/* YAML Editor - Outside Card, on the right side */}
          <GridItem span={3}>
            <div style={{ height: '600px', display: 'flex', flexDirection: 'column' }}>
              <div style={{ marginBottom: 'var(--pf-t--global--spacer--md)' }}>
                <Flex justifyContent={{ default: 'justifyContentSpaceBetween' }} alignItems={{ default: 'alignItemsCenter' }}>
                  <FlexItem>
                    <Title headingLevel="h3" size="md">Preflight YAML</Title>
                  </FlexItem>
                  <FlexItem>
                    <ActionGroup>
                      <Button variant="secondary" onClick={handleCopyYaml}>
                        Copy
                      </Button>
                      <Button variant="secondary" onClick={handleDownloadYaml}>
                        Download
                      </Button>
                      <Button variant="secondary" onClick={handleExportPreflightYaml}>
                        Export
                      </Button>
                    </ActionGroup>
                  </FlexItem>
                </Flex>
              </div>
              <div style={{ flex: 1, minHeight: 0 }}>
                <TextArea
                  value={yamlContent}
                  onChange={(_, value) => setYamlContent(value)}
                  rows={20}
                  resizeOrientation="vertical"
                  aria-label="YAML editor"
                  style={{ height: '100%' }}
                />
              </div>
            </div>
          </GridItem>
        </Grid>
      </PageSection>
    </>
  );
};

export { PreflightSummary };

