// Custom Footer Component for all steps with Auto-upgrade with AI
const WizardStepFooter: React.FunctionComponent<{
  onStartPreflight?: () => void;
  isReviewStep?: boolean;
  showBack?: boolean;
}> = ({ onStartPreflight, isReviewStep = false, showBack = true }) => {
  const { goToNextStep, goToPrevStep } = useWizardContext();

  return (
    <WizardFooterWrapper>
      <ActionList>
        <ActionListGroup>
          {showBack && (
            <ActionListItem>
              <Button variant="secondary" onClick={goToPrevStep}>
                Back
              </Button>
            </ActionListItem>
          )}
          {isReviewStep && onStartPreflight ? (
            <>
              <ActionListItem>
                <Button variant="primary" onClick={onStartPreflight}>
                  Start upgrade preflight
                </Button>
              </ActionListItem>
              <ActionListItem>
                <Button variant="secondary">Auto-upgrade with AI</Button>
              </ActionListItem>
            </>
          ) : (
            <>
              <ActionListItem>
                <Button variant="primary" onClick={goToNextStep}>
                  Next
                </Button>
              </ActionListItem>
              <ActionListItem>
                <Button variant="secondary">Auto-upgrade with AI</Button>
              </ActionListItem>
            </>
          )}
        </ActionListGroup>
      </ActionList>
    </WizardFooterWrapper>
  );
};

// Review Step Component
