import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import {
  PageSection,
  Title,
  Breadcrumb,
  BreadcrumbItem,
  Stack,
  StackItem,
  Content,
  Button,
  Flex,
  FlexItem,
  Card,
  CardTitle,
  CardBody,
  Grid,
  GridItem,
  Radio,
  Form,
  Select,
  SelectOption,
  MenuToggle,
  Checkbox,
  Divider,
  ToggleGroup,
  ToggleGroupItem,
  Pagination,
  Wizard,
  WizardStep,
  WizardBody,
  WizardFooterWrapper,
  useWizardContext,
  ActionList,
  ActionListGroup,
  ActionListItem,
  TextArea,
  Alert,
  Icon,
  Spinner,
  Progress,
  ProgressMeasureLocation,
} from '@patternfly/react-core';
import { Table, Thead, Tbody, Tr, Th, Td } from '@patternfly/react-table';
import { CheckCircleIcon, ExclamationTriangleIcon, ClockIcon, PauseIcon, FileAltIcon, PlayIcon } from '@patternfly/react-icons';

const ConfigureUpdate: React.FunctionComponent = () => {
  const navigate = useNavigate();
  
  // State for version selection
  const currentChannel = 'stable';
  const otherChannel = 'fast';
  const versionOptions = {
    stable: ['5.00.01', '5.00.02', '4.20.0', '4.19.0'],
    fast: ['5.01.00', '5.02.00', '5.03.00'],
  };
  const [upgradeOption, setUpgradeOption] = React.useState<'current-channel' | 'switch-channel'>('current-channel');
  const [selectedVersion, setSelectedVersion] = React.useState('4.20.0');
  const [currentChannelVersionDropdownOpen, setCurrentChannelVersionDropdownOpen] = React.useState(false);
  const [switchChannelVersionDropdownOpen, setSwitchChannelVersionDropdownOpen] = React.useState(false);
  
  // State for operators and nodes
  const [includeOperators, setIncludeOperators] = React.useState(false);
  const [includeNodeUpdate, setIncludeNodeUpdate] = React.useState(false);
  const [operatorFilter, setOperatorFilter] = React.useState<'all' | 'new-version'>('all');
  const [nodeFilter, setNodeFilter] = React.useState<'all' | 'new-version'>('all');
  const [operatorPage, setOperatorPage] = React.useState(1);
  const [operatorPerPage, setOperatorPerPage] = React.useState(10);
  const [nodePage, setNodePage] = React.useState(1);
  const [nodePerPage, setNodePerPage] = React.useState(10);
  
  // State for preflight checks
  const [activePreflightTab, setActivePreflightTab] = React.useState<string>('node-capacity');
  const [yamlContent, setYamlContent] = React.useState(`kind: ClusterUpgrade
metadata:
  name: cluster-upgrade-preflight
spec:
  currentVersion: 4.18.16
  targetVersion: ${selectedVersion}
  clusterId: ocp-cluster-abc123-def456-ghi789
  upgradeSequence:
    - control-plane
    - core-operators
    - workers
    - optional-operators`);
  const [maxUnavailable, setMaxUnavailable] = React.useState('1');
  const [isMaxUnavailableOpen, setIsMaxUnavailableOpen] = React.useState(false);
  
  // State for upgrade progress
  const [canContinue, setCanContinue] = React.useState(false);
  const [blockerPage, setBlockerPage] = React.useState(1);
  const [blockerPerPage, setBlockerPerPage] = React.useState(10);
  const [operatorProgressPage, setOperatorProgressPage] = React.useState(1);
  const [operatorProgressPerPage, setOperatorProgressPerPage] = React.useState(10);
  const [nodeProgressPage, setNodeProgressPage] = React.useState(1);
  const [nodeProgressPerPage, setNodeProgressPerPage] = React.useState(10);
  const [activeProgressTab, setActiveProgressTab] = React.useState<string>('current-blockers');
  const [isUpgradeComplete, setIsUpgradeComplete] = React.useState(false);
  const [upgradeStarted, setUpgradeStarted] = React.useState(false);
  const [controlPlaneProgress, setControlPlaneProgress] = React.useState(100);
  const [coreOperatorsProgress, setCoreOperatorsProgress] = React.useState(56);
  const [workersProgress, setWorkersProgress] = React.useState(0);
  
  // Auto-complete upgrade over 5 seconds when upgrade is started
  React.useEffect(() => {
    if (!upgradeStarted) return;
    
    const startTime = Date.now();
    const duration = 5000; // 5 seconds
    const initialControlPlane = 100;
    const initialCoreOperators = 56;
    const initialWorkers = 0;
    
    const interval = setInterval(() => {
      const elapsed = Date.now() - startTime;
      const progress = Math.min(elapsed / duration, 1);
      
      // Animate progress bars from their initial values to 100%
      // Control Plane is already at 100%, so it stays there
      setControlPlaneProgress(initialControlPlane);
      // Core Operators animate from 56% to 100%
      setCoreOperatorsProgress(initialCoreOperators + (100 - initialCoreOperators) * progress);
      // Workers animate from 0% to 100%
      setWorkersProgress(initialWorkers + 100 * progress);
      
      if (progress >= 1) {
        setIsUpgradeComplete(true);
        setCanContinue(true);
        clearInterval(interval);
      }
    }, 50); // Update every 50ms for smooth animation
    
    return () => clearInterval(interval);
  }, [upgradeStarted]);

  const handleWizardStepChange = React.useCallback((_event: React.MouseEvent<HTMLButtonElement>, currentStep: { id: string | number }, _prevStep: { id: string | number }, _scope: unknown) => {
    // Handle step changes if needed
  }, []);

  // Sample data
  const operatorColumns = ['Name', 'Version', 'Cluster compatibility', 'Support', 'Last updated', 'Status'];
  const nodePoolColumns = ['Pool', 'Current version', 'Paused', 'Nodes', 'Version issues'];
  const blockerColumns = ['Issue', 'Actions'];
  const operatorProgressColumns = ['Operator', 'Status'];
  const workerNodeColumns = ['Worker Node', 'Upgrade Stage'];

  // Mock data - in real app, this would come from API
  const operatorRows = [
    { name: 'Operator 1', version: '1.0.0', compatibility: 'Compatible', support: 'Community', lastUpdated: '2024-01-01', status: 'Available' },
    { name: 'Operator 2', version: '2.0.0', compatibility: 'Compatible', support: 'Red Hat', lastUpdated: '2024-01-15', status: 'Available' },
  ];

  const nodePoolRows = [
    { pool: 'worker-pool-1', currentVersion: '4.18.16', paused: 'No', nodes: '3', versionIssues: 'None' },
    { pool: 'worker-pool-2', currentVersion: '4.18.16', paused: 'No', nodes: '2', versionIssues: 'Compatible' },
  ];

  // Dynamic blocker rows - empty after completion
  const allBlockerRows = isUpgradeComplete ? [] : [
    {
      issue: 'Node worker-south cannot drain PodDisruptionBudget',
      actions: (
        <Flex gap={{ default: 'gapMd' }}>
          <Button variant="link" isInline>View logs</Button>
          <Button variant="link" isInline>Remediate</Button>
        </Flex>
      ),
    },
  ];

  // Dynamic operator progress rows - all successful after completion
  const allOperatorProgressRows = isUpgradeComplete ? [
    { operator: 'Another op 1', status: 'Available', statusIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
    { operator: 'Another op 2', status: 'Available', statusIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
    { operator: 'Another op 3', status: 'Available', statusIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
    { operator: 'Another op 4', status: 'Available', statusIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
  ] : [
    { operator: 'Another op 1', status: 'Progressing', statusIcon: <Spinner size="sm" /> },
    { operator: 'Another op 2', status: 'Available', statusIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
    { operator: 'Another op 3', status: 'Progressing', statusIcon: <Spinner size="sm" /> },
    { operator: 'Another op 4', status: 'Available', statusIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
  ];

  // Dynamic worker node rows - all completed after completion
  const allWorkerNodeRows = isUpgradeComplete ? [
    { node: 'worker-west', stage: 'Complete', stageIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
    { node: 'worker-east', stage: 'Complete', stageIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
    { node: 'worker-north', stage: 'Complete', stageIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
    { node: 'worker-south', stage: 'Complete', stageIcon: <Icon status="success"><CheckCircleIcon /></Icon> },
  ] : [
    { node: 'worker-west', stage: 'Rebooting', stageIcon: <Spinner size="sm" /> },
    { node: 'worker-east', stage: 'Pending (Not Started)', stageIcon: <Icon><ClockIcon /></Icon> },
    { node: 'worker-north', stage: 'Rebooting', stageIcon: <Spinner size="sm" /> },
    { node: 'worker-south', stage: 'Pending (Not Started)', stageIcon: <Icon><ClockIcon /></Icon> },
  ];

  const verticalPreflightTabs = [
    { id: 'node-capacity', label: 'Node capacity', completed: true },
    { id: 'cluster-health', label: 'Cluster health', completed: true },
    { id: 'api-changes', label: 'API changes in next upgrade', completed: false },
    { id: 'operator-readiness', label: 'Operator readiness', completed: false },
  ];

  const clusterHealthColumns = ['Check', 'Status'];
  const clusterHealthRows = [
    { check: 'All nodes healthy', status: 'Pass' },
    { check: 'Storage available', status: 'Pass' },
    { check: 'Network connectivity', status: 'Pass' },
    { check: 'API server responsive', status: 'Pass' },
  ];

  const apiChangesColumns = ['API', 'Status', 'Action Required'];
  const apiChangesRows = [
    { api: 'apps/v1 Deployment', status: 'Ready', action: 'None' },
    { api: 'networking.k8s.io/v1 Ingress', status: 'Ready', action: 'None' },
    { api: 'rbac.authorization.k8s.io/v1 Role', status: 'Review', action: 'Verify permissions' },
  ];

  const operatorReadinessColumns = ['Operator', 'Status', 'Version'];
  const operatorReadinessRows = [
    { operator: 'Red Hat OpenShift Service Mesh', status: 'Ready', version: '2.4.0' },
    { operator: 'Red Hat OpenShift Logging', status: 'Ready', version: '5.8.0' },
    { operator: 'Red Hat OpenShift GitOps', status: 'Pending', version: '1.11.0' },
  ];

  const verticalProgressTabs = [
    { 
      id: 'current-blockers', 
      label: 'Current Blockers', 
      completed: isUpgradeComplete, 
      statusIcon: isUpgradeComplete ? <Icon status="success"><CheckCircleIcon /></Icon> : <ExclamationTriangleIcon style={{ color: 'var(--pf-t--global--warning-color--100)' }} /> 
    },
    { 
      id: 'operator-upgrade', 
      label: 'Operator upgrade details', 
      completed: isUpgradeComplete, 
      statusIcon: isUpgradeComplete ? <Icon status="success"><CheckCircleIcon /></Icon> : <Spinner size="sm" /> 
    },
    { 
      id: 'worker-nodes', 
      label: 'Worker nodes upgrade details', 
      completed: isUpgradeComplete, 
      statusIcon: isUpgradeComplete ? <Icon status="success"><CheckCircleIcon /></Icon> : <Spinner size="sm" /> 
    },
  ];

  // Custom Footer Component for Step 1
  const ConfigureUpdateStepFooter: React.FunctionComponent = () => {
    const { goToNextStep } = useWizardContext();
    return (
      <Button variant="primary" onClick={goToNextStep}>
        Next
      </Button>
    );
  };

  // Custom Footer Component for Preflight Last Step
  const PreflightLastStepFooter: React.FunctionComponent = () => {
    const { goToNextStep, goToPrevStep } = useWizardContext();

    const handleRunUpgrade = () => {
      // Reset progress states to initial values
      setIsUpgradeComplete(false);
      setCanContinue(false);
      setControlPlaneProgress(100); // Control Plane already complete
      setCoreOperatorsProgress(56); // Core Operators in progress
      setWorkersProgress(0); // Workers not started
      
      // Start the upgrade animation
      setUpgradeStarted(true);
      
      // Navigate to the next step
      goToNextStep();
    };

    return (
      <>
        <ActionListItem>
          <Button variant="secondary" onClick={goToPrevStep}>
            Back
          </Button>
        </ActionListItem>
        <ActionListItem>
          <Button variant="primary" onClick={handleRunUpgrade}>
            Run upgrade
          </Button>
        </ActionListItem>
        <ActionListItem>
          <Button variant="secondary" onClick={() => {
            // Handle export audit plan
          }}>
            Export audit plan
          </Button>
        </ActionListItem>
        <ActionListItem>
          <Button variant="secondary" onClick={() => navigate('/administration/updates')}>
            Go back
          </Button>
        </ActionListItem>
      </>
    );
  };

  // Custom Footer Component for Progress Step
  const ProgressStepFooter: React.FunctionComponent = () => {
    return (
      <ActionListItem>
        <Button variant="primary" icon={<PlayIcon />} onClick={() => navigate('/administration/updates/update-overview')} isDisabled={!canContinue}>
          Done
        </Button>
      </ActionListItem>
    );
  };

  return (
    <>
      <PageSection hasBodyWrapper={false}>
        <Breadcrumb>
          <BreadcrumbItem to="#" onClick={() => navigate('/administration/updates')}>
            Cluster Updates
          </BreadcrumbItem>
          <BreadcrumbItem isActive>Configure Update</BreadcrumbItem>
        </Breadcrumb>
        <Title headingLevel="h1" size="lg" style={{ marginTop: 'var(--pf-t--global--spacer--md)' }}>
          Configure Cluster Update
        </Title>
      </PageSection>

      <PageSection variant="secondary" hasBodyWrapper>
        <Wizard
          height={800}
          onStepChange={handleWizardStepChange}
        >
          {/* Main Step 1: Configure update */}
          <WizardStep
            id="configure-update"
            name="Configure update"
            body={{ hasNoPadding: false }}
            footer={
              <WizardFooterWrapper>
                <ActionList>
                  <ActionListGroup>
                    <ActionListItem>
                      <Button variant="secondary" onClick={() => navigate('/administration/updates')}>
                        Cancel
                      </Button>
                    </ActionListItem>
                    <ActionListItem>
                      <ConfigureUpdateStepFooter />
                    </ActionListItem>
                  </ActionListGroup>
                </ActionList>
              </WizardFooterWrapper>
            }
          >
            {/* Substeps will be handled within this step */}
            <WizardBody>
              <Stack hasGutter>
                <StackItem>
                  <Title headingLevel="h2" size="lg">Configure update</Title>
                  <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                    Configure your cluster upgrade settings and review available updates.
                  </Content>
                </StackItem>
                <StackItem>
                  <Divider />
                </StackItem>
                
                {/* Substep: Select target version */}
                <StackItem>
                  <Title headingLevel="h3" size="md">Select target version</Title>
                  <Form>
                    <Stack hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--md)' }}>
                      <StackItem>
                        <Radio
                          id="current-channel-wizard"
                          name="upgrade-option-wizard"
                          label={`Next versions in your current ${currentChannel} channel`}
                          isChecked={upgradeOption === 'current-channel'}
                          onChange={() => setUpgradeOption('current-channel')}
                        />
                        <div style={{ marginLeft: '24px', marginTop: '8px', width: '300px' }}>
                          <Select
                            toggle={(toggleRef) => (
                              <MenuToggle
                                ref={toggleRef}
                                onClick={() => setCurrentChannelVersionDropdownOpen(!currentChannelVersionDropdownOpen)}
                                isExpanded={currentChannelVersionDropdownOpen}
                                variant="default"
                                style={{ width: '100%' }}
                                isDisabled={upgradeOption !== 'current-channel'}
                              >
                                {selectedVersion || 'Select version'}
                              </MenuToggle>
                            )}
                            isOpen={currentChannelVersionDropdownOpen}
                            onOpenChange={(isOpen: boolean) => setCurrentChannelVersionDropdownOpen(isOpen)}
                            selected={selectedVersion}
                            onSelect={(_, value) => {
                              setSelectedVersion(value as string);
                              setCurrentChannelVersionDropdownOpen(false);
                            }}
                          >
                            {versionOptions[currentChannel as keyof typeof versionOptions]?.map((version) => (
                              <SelectOption key={version} value={version}>
                                {version}
                              </SelectOption>
                            ))}
                          </Select>
                        </div>
                      </StackItem>
                      <StackItem>
                        <Radio
                          id="switch-channel-wizard"
                          name="upgrade-option-wizard"
                          label={`Switch to ${otherChannel} channel`}
                          isChecked={upgradeOption === 'switch-channel'}
                          onChange={() => setUpgradeOption('switch-channel')}
                        />
                        <div style={{ marginLeft: '24px', marginTop: '8px', width: '300px' }}>
                          <Select
                            toggle={(toggleRef) => (
                              <MenuToggle
                                ref={toggleRef}
                                onClick={() => setSwitchChannelVersionDropdownOpen(!switchChannelVersionDropdownOpen)}
                                isExpanded={switchChannelVersionDropdownOpen}
                                variant="default"
                                style={{ width: '100%' }}
                                isDisabled={upgradeOption !== 'switch-channel'}
                              >
                                {selectedVersion && upgradeOption === 'switch-channel' ? selectedVersion : 'Select version'}
                              </MenuToggle>
                            )}
                            isOpen={switchChannelVersionDropdownOpen}
                            onOpenChange={(isOpen: boolean) => setSwitchChannelVersionDropdownOpen(isOpen)}
                            selected={selectedVersion && upgradeOption === 'switch-channel' ? selectedVersion : undefined}
                            onSelect={(_, value) => {
                              setSelectedVersion(value as string);
                              setSwitchChannelVersionDropdownOpen(false);
                            }}
                          >
                            {versionOptions[otherChannel as keyof typeof versionOptions]?.map((version) => (
                              <SelectOption key={version} value={version}>
                                {version}
                              </SelectOption>
                            ))}
                          </Select>
                        </div>
                      </StackItem>
                    </Stack>
                  </Form>
                </StackItem>

                <StackItem>
                  <Divider />
                </StackItem>

                {/* Substep: Configure operators */}
                <StackItem>
                  <Title headingLevel="h3" size="md">Configure operators upgrade (optional)</Title>
                  <Stack hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--md)' }}>
                    <StackItem>
                      <Checkbox
                        id="include-operators-wizard"
                        label="Include operator updates with this cluster upgrade"
                        isChecked={includeOperators}
                        onChange={(_, checked) => setIncludeOperators(checked)}
                      />
                    </StackItem>
                    {includeOperators && (
                      <>
                        <StackItem>
                          <Divider />
                        </StackItem>
                        <StackItem>
                          <Flex justifyContent={{ default: 'justifyContentSpaceBetween' }} style={{ marginBottom: 'var(--pf-t--global--spacer--md)' }}>
                            <FlexItem>
                              <ToggleGroup aria-label="Operator filter">
                                <ToggleGroupItem
                                  text="All available operators"
                                  isSelected={operatorFilter === 'all'}
                                  onChange={() => setOperatorFilter('all')}
                                />
                                <ToggleGroupItem
                                  text="New version available"
                                  isSelected={operatorFilter === 'new-version'}
                                  onChange={() => setOperatorFilter('new-version')}
                                />
                              </ToggleGroup>
                            </FlexItem>
                          </Flex>
                          <Table>
                            <Thead>
                              <Tr>
                                {operatorColumns.map((column) => (
                                  <Th key={column}>{column}</Th>
                                ))}
                              </Tr>
                            </Thead>
                            <Tbody>
                              {operatorRows.map((row, index) => (
                                <Tr key={index}>
                                  <Td>{row.name}</Td>
                                  <Td>{row.version}</Td>
                                  <Td>{row.compatibility}</Td>
                                  <Td>{row.support}</Td>
                                  <Td>{row.lastUpdated}</Td>
                                  <Td>{row.status}</Td>
                                </Tr>
                              ))}
                            </Tbody>
                          </Table>
                        </StackItem>
                      </>
                    )}
                  </Stack>
                </StackItem>

                <StackItem>
                  <Divider />
                </StackItem>

                {/* Substep: Configure worker nodes */}
                <StackItem>
                  <Title headingLevel="h3" size="md">Configure worker nodes upgrade (optional)</Title>
                  <Stack hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--md)' }}>
                    <StackItem>
                      <Checkbox
                        id="include-node-update-wizard"
                        label="Include node update with this cluster upgrade"
                        isChecked={includeNodeUpdate}
                        onChange={(_, checked) => setIncludeNodeUpdate(checked)}
                      />
                    </StackItem>
                    {includeNodeUpdate && (
                      <>
                        <StackItem>
                          <Divider />
                        </StackItem>
                        <StackItem>
                          <Flex justifyContent={{ default: 'justifyContentSpaceBetween' }} style={{ marginBottom: 'var(--pf-t--global--spacer--md)' }}>
                            <FlexItem>
                              <ToggleGroup aria-label="Node filter">
                                <ToggleGroupItem
                                  text="All available nodes"
                                  isSelected={nodeFilter === 'all'}
                                  onChange={() => setNodeFilter('all')}
                                />
                                <ToggleGroupItem
                                  text="New version available"
                                  isSelected={nodeFilter === 'new-version'}
                                  onChange={() => setNodeFilter('new-version')}
                                />
                              </ToggleGroup>
                            </FlexItem>
                          </Flex>
                          <Table>
                            <Thead>
                              <Tr>
                                {nodePoolColumns.map((column) => (
                                  <Th key={column}>{column}</Th>
                                ))}
                              </Tr>
                            </Thead>
                            <Tbody>
                              {nodePoolRows.map((row, index) => (
                                <Tr key={index}>
                                  <Td>{row.pool}</Td>
                                  <Td>{row.currentVersion}</Td>
                                  <Td>{row.paused}</Td>
                                  <Td>{row.nodes}</Td>
                                  <Td>{row.versionIssues}</Td>
                                </Tr>
                              ))}
                            </Tbody>
                          </Table>
                        </StackItem>
                      </>
                    )}
                  </Stack>
                </StackItem>
              </Stack>
            </WizardBody>
          </WizardStep>

          {/* Main Step 2: Pre-flight check */}
          <WizardStep
            id="preflight-check"
            name="Pre-flight check"
            body={{ hasNoPadding: false }}
            footer={
              <WizardFooterWrapper>
                <ActionList>
                  <ActionListGroup>
                    <PreflightLastStepFooter />
                  </ActionListGroup>
                </ActionList>
              </WizardFooterWrapper>
            }
          >
            <WizardBody>
              <Stack hasGutter>
                <StackItem>
                  <Title headingLevel="h2" size="lg">Pre-flight check</Title>
                  <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                    Review pre-flight checks before running the upgrade.
                  </Content>
                </StackItem>
                <StackItem>
                  <Divider />
                </StackItem>

                {/* Substep: Pre-flight summary */}
                <StackItem>
                  <Title headingLevel="h3" size="md">Pre-flight summary</Title>
                  <Stack hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--md)' }}>
                    <StackItem>
                      <Content>
                        <strong>Current cluster ID:</strong> ocp-cluster-abc123-def456-ghi789
                      </Content>
                    </StackItem>
                    <StackItem>
                      <Content>
                        <strong>Current version:</strong> 4.18.16
                      </Content>
                    </StackItem>
                    <StackItem>
                      <Content>
                        <strong>Target version:</strong> {selectedVersion}
                      </Content>
                    </StackItem>
                    <StackItem>
                      <Content>
                        <strong>Estimated duration:</strong> Typical time 30-90 minutes
                      </Content>
                    </StackItem>
                    <StackItem>
                      <Content>
                        <strong>Upgrade sequence:</strong> Control plane  Core operators  Workers  Optional operators
                      </Content>
                    </StackItem>
                    <StackItem>
                      <Alert variant="warning" isInline title="Warning: rolling upgrades going to take specified nodes offline" />
                    </StackItem>
                  </Stack>
                </StackItem>

                <StackItem>
                  <Divider />
                </StackItem>

                {/* Substep: Pre-flight checks (vertical tabs) */}
                <StackItem>
                  <Title headingLevel="h3" size="md">Pre-flight checks</Title>
                  <Grid hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--md)' }}>
                    <GridItem span={8}>
                      <Card>
                        <CardBody>
                          <Grid hasGutter>
                            <GridItem span={3}>
                              <div style={{ 
                                backgroundColor: 'var(--pf-t--global--BackgroundColor--200)', 
                                padding: 'var(--pf-t--global--spacer--md)',
                                borderRadius: 'var(--pf-t--global--BorderRadius--sm)',
                                height: '400px',
                                overflowY: 'auto'
                              }}>
                                <Stack hasGutter>
                                  {verticalPreflightTabs.map((tab) => (
                                    <StackItem key={tab.id}>
                                      <div
                                        onClick={() => setActivePreflightTab(tab.id)}
                                        style={{
                                          padding: 'var(--pf-t--global--spacer--sm) var(--pf-t--global--spacer--md)',
                                          cursor: 'pointer',
                                          backgroundColor: activePreflightTab === tab.id ? 'var(--pf-t--global--BackgroundColor--100)' : 'transparent',
                                          borderLeft: activePreflightTab === tab.id ? '4px solid #0066CC' : '4px solid transparent',
                                          borderRadius: 'var(--pf-t--global--BorderRadius--sm)',
                                          display: 'flex',
                                          alignItems: 'center',
                                          gap: 'var(--pf-t--global--spacer--sm)',
                                        }}
                                      >
                                        {tab.completed && (
                                          <CheckCircleIcon style={{ 
                                            color: '#3E8635', 
                                            fontSize: 'var(--pf-t--global--FontSize--md)',
                                            flexShrink: 0,
                                          }} />
                                        )}
                                        <Content style={{ 
                                          color: activePreflightTab === tab.id ? '#0066CC' : 'var(--pf-t--global--Color--100)',
                                          fontWeight: activePreflightTab === tab.id ? 600 : 400,
                                        }}>
                                          {tab.label}
                                        </Content>
                                      </div>
                                    </StackItem>
                                  ))}
                                </Stack>
                              </div>
                            </GridItem>
                            <GridItem span={9}>
                              {activePreflightTab === 'node-capacity' && (
                                <Stack hasGutter>
                                  <StackItem>
                                    <Title headingLevel="h3" size="md">Node capacity</Title>
                                    <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                      Ensures the cluster can safely drain and update worker nodes during the upgrade.
                                    </Content>
                                  </StackItem>
                                  <StackItem>
                                    <Grid hasGutter>
                                      <GridItem span={6}>
                                        <Flex alignItems={{ default: 'alignItemsFlexStart' }}>
                                          <FlexItem>
                                            <Icon status="warning" style={{ marginTop: '4px' }}>
                                              <ExclamationTriangleIcon />
                                            </Icon>
                                          </FlexItem>
                                          <FlexItem>
                                            <Title headingLevel="h4" size="md">Max Unavailable Node Configuration</Title>
                                            <Stack hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                              <StackItem>
                                                <Content>
                                                  <strong>Current setting:</strong>
                                                </Content>
                                                <div style={{ marginTop: '4px' }}>
                                                  <Select
                                                    toggle={(toggleRef) => (
                                                      <MenuToggle
                                                        ref={toggleRef}
                                                        onClick={() => setIsMaxUnavailableOpen(!isMaxUnavailableOpen)}
                                                        isExpanded={isMaxUnavailableOpen}
                                                        variant="default"
                                                      >
                                                        {maxUnavailable}
                                                      </MenuToggle>
                                                    )}
                                                    isOpen={isMaxUnavailableOpen}
                                                    onOpenChange={(isOpen: boolean) => setIsMaxUnavailableOpen(isOpen)}
                                                    selected={maxUnavailable}
                                                    onSelect={(_, value) => {
                                                      setMaxUnavailable(value as string);
                                                      setIsMaxUnavailableOpen(false);
                                                    }}
                                                  >
                                                    <SelectOption value="1">1</SelectOption>
                                                    <SelectOption value="2">2</SelectOption>
                                                    <SelectOption value="3">3</SelectOption>
                                                    <SelectOption value="4">4</SelectOption>
                                                    <SelectOption value="5">5</SelectOption>
                                                  </Select>
                                                </div>
                                              </StackItem>
                                              <StackItem>
                                                <Content>
                                                  <strong>Recommended: 5</strong>
                                                </Content>
                                              </StackItem>
                                              <StackItem>
                                                <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                                  Allowing more nodes to be updated at once speeds up the upgrade, as long as your workloads can handle it.
                                                </Content>
                                              </StackItem>
                                            </Stack>
                                          </FlexItem>
                                        </Flex>
                                      </GridItem>
                                      <GridItem span={6}>
                                        <Flex alignItems={{ default: 'alignItemsFlexStart' }}>
                                          <FlexItem>
                                            <Icon status="danger" style={{ marginTop: '4px' }}>
                                              <ExclamationTriangleIcon />
                                            </Icon>
                                          </FlexItem>
                                          <FlexItem>
                                            <Title headingLevel="h4" size="md">Available Capacity per Worker Pool</Title>
                                            <Stack hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                              <StackItem>
                                                <Content>
                                                  You need 1 XL node of free capacity per worker pool.
                                                </Content>
                                              </StackItem>
                                              <StackItem>
                                                <Content>
                                                  <strong>Total free capacity required:</strong> 8 nodes
                                                </Content>
                                              </StackItem>
                                              <StackItem>
                                                <Content>
                                                  <strong>Available capacity detected:</strong> 3 nodes
                                                </Content>
                                              </StackItem>
                                              <StackItem>
                                                <Button variant="link" isInline>
                                                  Scale up nodes
                                                </Button>
                                              </StackItem>
                                            </Stack>
                                          </FlexItem>
                                        </Flex>
                                      </GridItem>
                                    </Grid>
                                  </StackItem>
                                </Stack>
                              )}
                              {activePreflightTab === 'cluster-health' && (
                                <Stack hasGutter>
                                  <StackItem>
                                    <Title headingLevel="h3" size="md">Cluster health</Title>
                                    <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                      Verifies that all cluster components are healthy and ready for upgrade.
                                    </Content>
                                  </StackItem>
                                  <StackItem>
                                    <Table>
                                      <Thead>
                                        <Tr>
                                          {clusterHealthColumns.map((column) => (
                                            <Th key={column}>{column}</Th>
                                          ))}
                                        </Tr>
                                      </Thead>
                                      <Tbody>
                                        {clusterHealthRows.map((row, index) => (
                                          <Tr key={index}>
                                            <Td>{row.check}</Td>
                                            <Td>
                                              <Icon status="success">
                                                <CheckCircleIcon />
                                              </Icon>
                                              {' '}
                                              {row.status}
                                            </Td>
                                          </Tr>
                                        ))}
                                      </Tbody>
                                    </Table>
                                  </StackItem>
                                </Stack>
                              )}
                              {activePreflightTab === 'api-changes' && (
                                <Stack hasGutter>
                                  <StackItem>
                                    <Title headingLevel="h3" size="md">API changes in next upgrade</Title>
                                    <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                      Review API changes that may affect your workloads.
                                    </Content>
                                  </StackItem>
                                  <StackItem>
                                    <Table>
                                      <Thead>
                                        <Tr>
                                          {apiChangesColumns.map((column) => (
                                            <Th key={column}>{column}</Th>
                                          ))}
                                        </Tr>
                                      </Thead>
                                      <Tbody>
                                        {apiChangesRows.map((row, index) => (
                                          <Tr key={index}>
                                            <Td>{row.api}</Td>
                                            <Td>{row.status}</Td>
                                            <Td>{row.action}</Td>
                                          </Tr>
                                        ))}
                                      </Tbody>
                                    </Table>
                                  </StackItem>
                                </Stack>
                              )}
                              {activePreflightTab === 'operator-readiness' && (
                                <Stack hasGutter>
                                  <StackItem>
                                    <Title headingLevel="h3" size="md">Operator readiness</Title>
                                    <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                                      Verify that all operators are ready for the upgrade.
                                    </Content>
                                  </StackItem>
                                  <StackItem>
                                    <Table>
                                      <Thead>
                                        <Tr>
                                          {operatorReadinessColumns.map((column) => (
                                            <Th key={column}>{column}</Th>
                                          ))}
                                        </Tr>
                                      </Thead>
                                      <Tbody>
                                        {operatorReadinessRows.map((row, index) => (
                                          <Tr key={index}>
                                            <Td>{row.operator}</Td>
                                            <Td>{row.status}</Td>
                                            <Td>{row.version}</Td>
                                          </Tr>
                                        ))}
                                      </Tbody>
                                    </Table>
                                  </StackItem>
                                </Stack>
                              )}
                            </GridItem>
                          </Grid>
                        </CardBody>
                      </Card>
                    </GridItem>
                    <GridItem span={4}>
                      <Card>
                        <CardTitle>Preflight YAML</CardTitle>
                        <CardBody>
                          <Stack hasGutter>
                            <StackItem>
                              <Flex gap={{ default: 'gapMd' }}>
                                <Button variant="secondary" size="sm">Copy</Button>
                                <Button variant="secondary" size="sm">Download</Button>
                                <Button variant="secondary" size="sm">Export</Button>
                              </Flex>
                            </StackItem>
                            <StackItem>
                              <TextArea
                                value={yamlContent}
                                onChange={(_, value) => setYamlContent(value)}
                                rows={15}
                                readOnly
                              />
                            </StackItem>
                          </Stack>
                        </CardBody>
                      </Card>
                    </GridItem>
                  </Grid>
                </StackItem>
              </Stack>
            </WizardBody>
          </WizardStep>

          {/* Main Step 3: Cluster update */}
          <WizardStep
            id="cluster-update"
            name="Cluster update"
            body={{ hasNoPadding: false }}
            footer={
              <WizardFooterWrapper>
                <ActionList>
                  <ActionListGroup>
                    <ProgressStepFooter />
                  </ActionListGroup>
                </ActionList>
              </WizardFooterWrapper>
            }
          >
            <WizardBody>
              <Stack hasGutter>
                <StackItem>
                  <Title headingLevel="h2" size="lg">Cluster update</Title>
                  <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                    Monitor the progress of your cluster upgrade.
                  </Content>
                </StackItem>
                <StackItem>
                  <Divider />
                </StackItem>

                {/* Status and Summary */}
                <StackItem>
                  <Stack hasGutter>
                    <StackItem>
                      <Flex alignItems={{ default: 'alignItemsCenter' }} gap={{ default: 'gapMd' }}>
                        {!isUpgradeComplete && <Spinner size="md" />}
                        {isUpgradeComplete && <Icon status="success"><CheckCircleIcon /></Icon>}
                        <Title headingLevel="h3" size="lg">Upgrade to {selectedVersion} Progress</Title>
                      </Flex>
                      <Content style={{ marginTop: 'var(--pf-t--global--spacer--sm)' }}>
                        <strong>Cluster:</strong> 1c182977-5663-428d-92a3-3d2bdf3fffb6
                      </Content>
                      <Content style={{ marginTop: 'var(--pf-t--global--spacer--xs)' }}>
                        <strong>Start time:</strong> 2025-11-14 21:32:00
                      </Content>
                      <Content style={{ marginTop: 'var(--pf-t--global--spacer--xs)' }}>
                        <strong>Estimated time remaining:</strong> 84 min
                      </Content>
                      <Content style={{ marginTop: 'var(--pf-t--global--spacer--xs)' }}>
                        <strong>Estimated finish time:</strong> 2025-11-14 22:56:00
                      </Content>
                    </StackItem>
                    <StackItem>
                      <Grid hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--lg)' }}>
                        <GridItem span={4}>
                          <div style={{ marginBottom: 'var(--pf-t--global--spacer--sm)' }}>
                            <Title headingLevel="h4" size="md">Control Plane</Title>
                          </div>
                          <Progress
                            value={controlPlaneProgress}
                            measureLocation={ProgressMeasureLocation.inside}
                            label={controlPlaneProgress === 100 ? "Complete" : undefined}
                            aria-label="Control Plane progress"
                            style={{ height: '24px' }}
                            className={controlPlaneProgress === 100 ? "pf-v6-c-progress--success" : ""}
                          />
                        </GridItem>
                        <GridItem span={4}>
                          <div style={{ marginBottom: 'var(--pf-t--global--spacer--sm)' }}>
                            <Title headingLevel="h4" size="md">Core Operators</Title>
                          </div>
                          <Progress
                            value={coreOperatorsProgress}
                            measureLocation={ProgressMeasureLocation.inside}
                            aria-label="Core Operators progress"
                            style={{ height: '24px' }}
                            className={coreOperatorsProgress === 0 ? "pf-v6-c-progress--zero-percent" : coreOperatorsProgress === 100 ? "pf-v6-c-progress--success" : ""}
                          />
                        </GridItem>
                        <GridItem span={4}>
                          <div style={{ marginBottom: 'var(--pf-t--global--spacer--sm)' }}>
                            <Title headingLevel="h4" size="md">Workers</Title>
                          </div>
                          <Progress
                            value={workersProgress}
                            measureLocation={ProgressMeasureLocation.inside}
                            aria-label="Workers progress"
                            className={workersProgress === 0 ? "pf-v6-c-progress--zero-percent" : workersProgress === 100 ? "pf-v6-c-progress--success" : ""}
                            style={{ height: '24px' }}
                          />
                        </GridItem>
                      </Grid>
                    </StackItem>
                  </Stack>
                </StackItem>

                <StackItem>
                  <Divider />
                </StackItem>

                {/* Vertical tabs for details */}
                <StackItem>
                  <Card>
                    <CardBody>
                      <Grid hasGutter>
                        <GridItem span={3}>
                          <div style={{ 
                            backgroundColor: 'var(--pf-t--global--BackgroundColor--200)', 
                            padding: 'var(--pf-t--global--spacer--md)',
                            borderRadius: 'var(--pf-t--global--BorderRadius--sm)',
                            height: '400px',
                            overflowY: 'auto'
                          }}>
                            <Stack hasGutter>
                              {verticalProgressTabs.map((tab) => (
                                <StackItem key={tab.id}>
                                  <div
                                    onClick={() => setActiveProgressTab(tab.id)}
                                    style={{
                                      padding: 'var(--pf-t--global--spacer--sm) var(--pf-t--global--spacer--md)',
                                      cursor: 'pointer',
                                      backgroundColor: activeProgressTab === tab.id ? 'var(--pf-t--global--BackgroundColor--100)' : 'transparent',
                                      borderLeft: activeProgressTab === tab.id ? '4px solid #0066CC' : '4px solid transparent',
                                      borderRadius: 'var(--pf-t--global--BorderRadius--sm)',
                                      display: 'flex',
                                      alignItems: 'center',
                                      gap: 'var(--pf-t--global--spacer--sm)',
                                    }}
                                  >
                                    {tab.statusIcon && (
                                      <div style={{ flexShrink: 0 }}>
                                        {tab.statusIcon}
                                      </div>
                                    )}
                                    <Content style={{ 
                                      color: activeProgressTab === tab.id ? '#0066CC' : 'var(--pf-t--global--Color--100)',
                                      fontWeight: activeProgressTab === tab.id ? 600 : 400,
                                    }}>
                                      {tab.label}
                                    </Content>
                                  </div>
                                </StackItem>
                              ))}
                            </Stack>
                          </div>
                        </GridItem>
                        <GridItem span={9}>
                          {activeProgressTab === 'current-blockers' && (
                            <Stack hasGutter>
                              <StackItem>
                                <Title headingLevel="h3" size="md">Current Blockers</Title>
                              </StackItem>
                              <StackItem>
                                {allBlockerRows.length === 0 ? (
                                  <Content>
                                    <Icon status="success">
                                      <CheckCircleIcon />
                                    </Icon>
                                    {' '}
                                    No blockers detected. Upgrade completed successfully.
                                  </Content>
                                ) : (
                                  <>
                                    <Table>
                                      <Thead>
                                        <Tr>
                                          {blockerColumns.map((column) => (
                                            <Th key={column}>{column}</Th>
                                          ))}
                                        </Tr>
                                      </Thead>
                                      <Tbody>
                                        {(() => {
                                          const startIdx = (blockerPage - 1) * blockerPerPage;
                                          const endIdx = startIdx + blockerPerPage;
                                          const paginatedRows = allBlockerRows.slice(startIdx, endIdx);
                                          return paginatedRows.map((row, index) => (
                                            <Tr key={index}>
                                              <Td>
                                                <Flex alignItems={{ default: 'alignItemsCenter' }} gap={{ default: 'gapSm' }}>
                                                  <ExclamationTriangleIcon style={{ color: 'var(--pf-t--global--warning-color--100)' }} />
                                                  <Content>{row.issue}</Content>
                                                </Flex>
                                              </Td>
                                              <Td>{row.actions}</Td>
                                            </Tr>
                                          ));
                                        })()}
                                      </Tbody>
                                    </Table>
                                    {allBlockerRows.length > blockerPerPage && (
                                      <StackItem>
                                        <Pagination
                                          itemCount={allBlockerRows.length}
                                          perPage={blockerPerPage}
                                          page={blockerPage}
                                          onSetPage={(_, page) => setBlockerPage(page)}
                                          onPerPageSelect={(_, perPage) => {
                                            setBlockerPerPage(perPage);
                                            setBlockerPage(1);
                                          }}
                                        />
                                      </StackItem>
                                    )}
                                  </>
                                )}
                              </StackItem>
                            </Stack>
                          )}
                          {activeProgressTab === 'operator-upgrade' && (
                            <Stack hasGutter>
                              <StackItem>
                                <Title headingLevel="h3" size="md">Operator upgrade details</Title>
                              </StackItem>
                              <StackItem>
                                <Table>
                                  <Thead>
                                    <Tr>
                                      {operatorProgressColumns.map((column) => (
                                        <Th key={column}>{column}</Th>
                                      ))}
                                    </Tr>
                                  </Thead>
                                  <Tbody>
                                    {(() => {
                                      const startIdx = (operatorProgressPage - 1) * operatorProgressPerPage;
                                      const endIdx = startIdx + operatorProgressPerPage;
                                      const paginatedRows = allOperatorProgressRows.slice(startIdx, endIdx);
                                      return paginatedRows.map((row, index) => (
                                        <Tr key={index}>
                                          <Td>{row.operator}</Td>
                                          <Td>
                                            <Flex alignItems={{ default: 'alignItemsCenter' }} gap={{ default: 'gapSm' }}>
                                              {row.statusIcon}
                                              <Content>{row.status}</Content>
                                            </Flex>
                                          </Td>
                                        </Tr>
                                      ));
                                    })()}
                                  </Tbody>
                                </Table>
                              </StackItem>
                              {allOperatorProgressRows.length > operatorProgressPerPage && (
                                <StackItem>
                                  <Pagination
                                    itemCount={allOperatorProgressRows.length}
                                    perPage={operatorProgressPerPage}
                                    page={operatorProgressPage}
                                    onSetPage={(_, page) => setOperatorProgressPage(page)}
                                    onPerPageSelect={(_, perPage) => {
                                      setOperatorProgressPerPage(perPage);
                                      setOperatorProgressPage(1);
                                    }}
                                  />
                                </StackItem>
                              )}
                            </Stack>
                          )}
                          {activeProgressTab === 'worker-nodes' && (
                            <Stack hasGutter>
                              <StackItem>
                                <Title headingLevel="h3" size="md">Worker nodes upgrade details</Title>
                              </StackItem>
                              <StackItem>
                                <Table>
                                  <Thead>
                                    <Tr>
                                      {workerNodeColumns.map((column) => (
                                        <Th key={column}>{column}</Th>
                                      ))}
                                    </Tr>
                                  </Thead>
                                  <Tbody>
                                    {(() => {
                                      const startIdx = (nodeProgressPage - 1) * nodeProgressPerPage;
                                      const endIdx = startIdx + nodeProgressPerPage;
                                      const paginatedRows = allWorkerNodeRows.slice(startIdx, endIdx);
                                      return paginatedRows.map((row, index) => (
                                        <Tr key={index}>
                                          <Td>{row.node}</Td>
                                          <Td>
                                            <Flex alignItems={{ default: 'alignItemsCenter' }} gap={{ default: 'gapSm' }}>
                                              {row.stageIcon}
                                              <Content>{row.stage}</Content>
                                            </Flex>
                                          </Td>
                                        </Tr>
                                      ));
                                    })()}
                                  </Tbody>
                                </Table>
                              </StackItem>
                              {allWorkerNodeRows.length > nodeProgressPerPage && (
                                <StackItem>
                                  <Pagination
                                    itemCount={allWorkerNodeRows.length}
                                    perPage={nodeProgressPerPage}
                                    page={nodeProgressPage}
                                    onSetPage={(_, page) => setNodeProgressPage(page)}
                                    onPerPageSelect={(_, perPage) => {
                                      setNodeProgressPerPage(perPage);
                                      setNodeProgressPage(1);
                                    }}
                                  />
                                </StackItem>
                              )}
                            </Stack>
                          )}
                        </GridItem>
                      </Grid>
                    </CardBody>
                  </Card>
                </StackItem>
              </Stack>
            </WizardBody>
          </WizardStep>
        </Wizard>
      </PageSection>
    </>
  );
};

export { ConfigureUpdate };

